<template>
  <div id="policies" class="select2-disable">
    <div class="clear__container">
      <a href="#" class="clear" v-if="filtering" @click.prevent="resetTable"
        >Clear filters</a
      >
    </div>
    <v-good-table
      :columns="columns"
      :rows="rows"
      ref="policies"
      @on-column-filter="handleColumnFilter"
      styleClass="vgt-table sorting-asc"
      :sort-options="{
        enabled: true,
        initialSortBy: { field: 'number', type: 'asc' },
      }"
      :pagination-options="{
        enabled: true,
        mode: 'records',
        perPage: 75,
        perPageDropdown: [75, 150],
      }"
    >
      <div slot="emptystate">
        No results.
      </div>
      <template slot="table-row" slot-scope="props">
        <span v-if="props.column.field == 'title'"
          ><a
            :href="
              `https://osteopathic.org/wp-content/uploads/policies/${
                props.row.filename
              }`
            "
            target="_blank"
            >{{ props.row.title }}</a
          ></span
        >
        <span v-else>{{ props.formattedRow[props.column.field] }}</span>
      </template>
    </v-good-table>
  </div>
</template>

<script>
import { VueGoodTable } from 'vue-good-table';
import 'vue-good-table/dist/vue-good-table.css';
//import resolutions from './../resolutions-2020-12-12.json';
import policies from './../policies-2016-out.json';
import sortedUniq from 'lodash/sortedUniq';
import sortBy from 'lodash/sortBy';

export default {
  components: {
    'v-good-table': VueGoodTable,
  },

  name: 'app',

  mounted() {
    //    this.$set(
    //      this.rows,
    //      policies.map(res => {
    //        const commentHtml =
    //          res.gridComments +
    //          '<span class="cell-note">Assigned to ' +
    //          res.gridAssigned +
    //          '</span>';
    //        res.gridComments = commentHtml;
    //
    //        return res;
    //      })
    //    );
  },

  methods: {
    findInArray(arr, cb) {
      for (var i = 0, len = arr.length; i < len; i++) {
        if (cb(arr[i])) {
          return arr[i];
          break;
        }
      }

      return false;
    },

    resetTable() {
      this.filtering = false;

      const selects = document.querySelectorAll('.vgt-select, .vgt-input');

      for (var i = 0, len = selects.length; i < len; i++) {
        selects[i].classList.remove('filtering');
        this.resetColumnPlaceholderText(i, selects);
      }

      this.$refs['policies'].reset();
    },

    resetColumnPlaceholderText(index, selects) {
      const currentCol = this.findInArray(this.columns, item => {
        return item.field == this.filterableColumns[index];
      });

      // Add default placeholder text
      if (
        currentCol &&
        currentCol.filterOptions.hasOwnProperty('filterDropdownItems')
      ) {
        selects[index].options[0].innerText =
          currentCol.filterOptions.placeholder;
      }
    },

    handleColumnFilter(params) {
      const columnFilters = params.columnFilters;
      const filters = document.querySelectorAll('.vgt-select, .vgt-input');

      this.filtering = false;

      for (var column in columnFilters) {
        const index = this.filterableColumns.indexOf(column);

        if (columnFilters.hasOwnProperty(column)) {
          if (index > -1) {
            if (filters[index].value) {
              // We're filtering on this column
              filters[index].classList.add('filtering');

              if (filters[index].nodeName === 'SELECT') {
                filters[index].options[0].innerText = 'Reset';
              }
              this.filtering = true;
            } else {
              // We're no longer filtering on this column
              filters[index].classList.remove('filtering');

              this.resetColumnPlaceholderText(index, filters);
            }
          }
        }
      }
    },

    formatNumber(str) {
      const matches = str.match(/H-\w*/g);

      if (matches && matches.length) {
        return matches[0];
      }

      return str;
    },

    sortResolutionNumbers(x, y) {
      const xMatch = x.match(/\d+/g);
      const yMatch = y.match(/\d+/g);

      if (xMatch && yMatch) {
        const x2 = +xMatch[0];
        const y2 = +yMatch[0];

        return x2 - y2;
      }

      return 0;
    },

    commentsFieldFn(row) {
      return (
        row.gridComments +
        '<span style="color: fuchsia">' +
        row.gridAssigned +
        '</span>'
      );
    },
  },

  computed: {
    filterableColumns: function() {
      return this.columns
        .filter(item => {
          return (
            item.hasOwnProperty('filterOptions') && item.filterOptions.enabled
          );
        })
        .map(item => item.field);
    },
  },

  data() {
    return {
      columns: [
        {
          label: 'Year of Adoption',
          field: 'year',
          width: '15%',
          //          filterOptions: {
          //            enabled: true,
          //            filterFn: (data, filterString) => data == filterString,
          //            filterDropdownItems: ['2016'],
          //            placeholder: 'Select',
          //          },
        },
        {
          label: 'Source #',
          field: 'number',
          width: '12%',
          formatFn: this.formatNumber,
          sortFn: this.sortResolutionNumbers,
        },
        {
          label: 'Policy Title',
          field: 'title',
          width: '35%',
          filterOptions: {
            enabled: true,
          },
        },
        {
          label: 'House Action',
          field: 'action',
          //          filterOptions: {
          //            enabled: true,
          //            placeholder: 'Filter action',
          //          },
          width: '18%',
        },
      ],

      rows: policies,
      filtering: false,
    };
  },
};
</script>
<style lang="scss">
$blue: #006;
$gold: #fa0;
$seaserpent: #11c7eb;
$warm: #f9f7f5;
$font-color: #2a2a2a;
$border-color: #d0d0d0;

#policies {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  font-family: 'proxima-nova', Proxima Nova, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $font-color;

  h1,
  h2 {
    font-weight: 700;
    color: $blue;
  }

  h2 {
    text-align: left;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  .clear__container {
    position: relative;
    margin-bottom: 30px;
    text-align: center;
  }

  .clear {
    padding: 7px 17px;
    background-color: #fff;
    border: 1px solid $gold;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.6s;

    @media screen and (min-width: 992px) {
      position: absolute;
      top: 0;
      right: 0;
    }

    &:link,
    &:visited {
      text-decoration: none;
      color: $blue;
      font-weight: 700;
    }

    &:hover,
    &:active {
      background-color: $blue;
      color: #fff;
      border-color: $blue;
    }

    &--empty {
      &:link,
      &:visited {
        @extend .clear:link;
      }
    }
  }

  .filtering:focus,
  .filtering {
    color: $blue;
    font-weight: 700;
    border-color: $gold;
  }

  .u-centered {
    text-align: center;
  }

  .cell-note {
    display: block;
    font-size: 90%;
    color: #777;
  }

  /**
   * Osteopathic.org overrides
   */
  table tbody td {
    border-left: none;

    &:last-child {
      border-right: none;
    }
  }

  select {
    min-width: auto;
    line-height: normal;
    appearance: menulist;
    -webkit-appearance: menulist;
    -moz-appearance: menulist;
    background: none;
    background-color: #fff;

    &::-ms-expand {
      display: none;
    }
  }

  /**
   * VGT Overrides
   */

  .vgt-table {
    border-color: $border-color;
  }

  .vgt-table td {
    color: $font-color;
    letter-spacing: 0;
    line-height: 1.2;
  }

  .vgt-table thead th.sorting-asc,
  .vgt-table thead th.sorting-desc,
  .vgt-table thead th {
    color: $blue;
    background: $warm;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    text-transform: none;
    letter-spacing: 0;
  }

  .vgt-table th.sorting:after {
    top: 84%;
  }

  .vgt-table tr:first-child th:not(.sorting):hover {
    cursor: pointer;

    &:after {
      content: '';
      position: absolute;
      height: 0px;
      width: 0px;
      right: 6px;
      top: 84%;
      margin-top: -3px;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 6px solid lighten($gold, 25%);
    }
  }

  .vgt-wrap__footer {
    background: $warm;
    color: $font-color;
    border-color: $border-color;
  }

  .footer__navigation__page-btn {
    color: $blue;

    &.disabled {
      color: #909399;
    }
  }

  .chevron.right:after,
  .chevron.left:after {
    border-left-color: $gold;
    border-right-color: $gold;
  }

  //  .vgt-input {
  //    font-weight: 700;
  //    color: $blue;
  //
  //    &::-webkit-input-placeholder {
  //      font-weight: 400;
  //    }
  //
  //    &::-ms-placeholder {
  //      font-weight: 400;
  //    }
  //
  //    &::-moz-placeholder {
  //      font-weight: 400;
  //
  //    }
  //
  //    &::-moz-input-placeholder {
  //      font-weight: 400;
  //    }
  //  }

  .vgt-input,
  .vgt-select {
    padding: 6px 12px !important;

    &:focus {
      border-color: $gold;
    }
  }

  .vgt-table th.sorting {
    &:after {
      border-top-color: $gold;
      border-bottom-color: $gold;
    }

    &:hover:after {
      display: block;
    }
  }

  .vgt-table th.sorting:last-child {
    &:after {
      //      position: relative;
      //     margin-top: 4px;
      //      left: 50%;
      //      transform: translateX(-50%);

      @media screen and (min-width: 992px) {
        //       position: absolute;
        //       transform: none;
        //       left: auto;
        //       margin-top: 0;
      }
    }
  }

  .vgt-global-search {
    background: $warm;
  }

  a[href$='.pdf']:not(.no-icon):before {
    content: none;
  }

  a[href$='.pdf']:not(.no-icon):after {
    content: '';
    position: relative;
    top: -2px;
    width: 17px;
    height: 17px;
    margin-left: 4px;
    background-image: url(https://osteopathic.org/wp-content/themes/osteopathic/dist/images/icon-pdf-8.png);
    background-size: contain;
    background-repeat: no-repeat;
    display: inline-block;
    vertical-align: middle;
  }
  .cell-note {
    display: block;
    margin-top: 4px;
    font-size: 90%;
    color: #777;
  }
}
</style>



